import React, { Component } from 'react';
import { H1, H3, H4, H5, H6, Span, Btn } from '../css/fonts.js';
import { OuterContainer, InnerContainer, OuterFlexBox, Img, Card } from '../css/container.js';
import { projectSummary } from '../content.js';
import Navbar from '../components/Navbar.js';
import Footer from '../components/Footer';
import './style.css';

class Home extends Component {
  constructor() {
    super();
    this.state={
      hover: ""
    }
  }

  hover = e => {
    const target = e.currentTarget.id;
    if (this.state.hover === target) {
      this.setState({hover: ""});
    }
    else {
      this.setState({hover: target});
    }
  }


  handleClick = e => {
    const target = document.getElementById("takeaways");
    console.log("target");
    console.log(e.target.title);
    target.scrollIntoView();
  }

  render() {
    return (
      <div>
        <Navbar/>
        <OuterContainer>
          <InnerContainer>
            <br/><br/><br/><br/><br/><br/>
            <H1>I'm Yuhan (Hannah) Wang. Welcome to my space!</H1><br/>
            <H4>I see myself as a
              <Span> UX Specialist </Span> and a
              <Span> Problem solver</Span>.
              <br/>
              I am always motivated to
              <Span> discover the right problem </Span>
              and <Span> create impactful solutions through empathizing</Span>.
            </H4>
            <br/>
            {/**************************Projects*************************/}
            <H1 bold>Portfolio</H1>
            <br/>
            <OuterFlexBox>
              {projectSummary.map(project => {
                const id = project.id;
                return (
                <Card
                  key={project.id}
                  onMouseEnter={this.hover}
                  onMouseLeave={this.hover}
                  id={project.id}
                  theme={{widthM:"48%", widthS: "100%"}}
                  to={project.link}
                >
                  <div>
                    <Img src={project.img} alt={project.img} theme={{width: "100%"}}/>
                    <br/><br/>
                    <div style={{"padding":"0 30px 20px 30px"}}>
                    <H3>{project.title}</H3>
                    <H5>{project.subtitle}</H5>
                    <H6>My ROLE</H6>
                    <H5 dangerouslySetInnerHTML={{ __html: project.role }} />
                    <br/>
                    <Btn to={project.link} hover={this.state.hover === id}>
                      Learn More
                    </Btn>
                    </div>
                  </div>
                  <br/>
                </Card>
              )})}
            </OuterFlexBox>
          </InnerContainer>
        </OuterContainer>
        <Footer/>
      </div>
    );
  }
}

export default Home;
