import React, { Component } from 'react';
import { H1, H2, H4, H5, Span, NextBtn } from '../css/fonts.js';
import { OuterContainer, InnerContainer, Img, TwoCol } from '../css/container.js';
import { capitalsphereContent } from '../content.js';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';

class Capitalsphere extends Component {
  render() {
    return (
      <div>
        <Navbar/>
        <OuterContainer>
          <InnerContainer>
            <br/><br/><br/><br/><br/>
            <H1>{capitalsphereContent.title}<Span subtitle>, {capitalsphereContent.subtitle}</Span></H1>
            <H5 light>Team: {capitalsphereContent.team}</H5>
            <H5 dangerouslySetInnerHTML={{ __html: capitalsphereContent.intro }} />
            <br/>
            <TwoCol>
              <div>
                <H2>My Role</H2>
                <H5 dangerouslySetInnerHTML={{ __html: capitalsphereContent.role }} />
              </div>
              <div>
                <H2>Skills</H2>
                <H5 dangerouslySetInnerHTML={{ __html: capitalsphereContent.skills }} />
              </div>
            </TwoCol>
            <br/>
            <H2>Challenge</H2>
            <H5 dangerouslySetInnerHTML={{ __html: capitalsphereContent.challenge }} />
            <H2>My Strategy</H2>
            <H5 dangerouslySetInnerHTML={{ __html: capitalsphereContent.strategy }} />
            <br/><br/>
            <H2>Early stage UI/UX Design</H2>
            <H4 bold>Stakeholder interview</H4>
            <TwoCol>
              <H5 dangerouslySetInnerHTML={{ __html: capitalsphereContent.step1 }} />
              <Img border src={capitalsphereContent.img1_1} alt={capitalsphereContent.img1_1} theme={{width:"100%"}}/>
            </TwoCol>
            <br/>
            <H4 bold>Competitive Analysis</H4>
            <TwoCol>
              <H5 dangerouslySetInnerHTML={{ __html: capitalsphereContent.step2 }} />
              <Img border src={capitalsphereContent.img2_1} alt={capitalsphereContent.img2_1} theme={{width:"100%"}}/>
            </TwoCol>
            <br/><br/>
            <H4 bold>Information Architecture</H4>
            <H5 dangerouslySetInnerHTML={{ __html: capitalsphereContent.step3 }} />
            <TwoCol even>
              <Img border src={capitalsphereContent.img3_1} alt={capitalsphereContent.img3_1} theme={{width:"100%"}}/>
              <Img border src={capitalsphereContent.img3_2} alt={capitalsphereContent.img3_2} theme={{width:"100%"}}/>
            </TwoCol>
            <br/><br/>
            <H4 bold>Prototype</H4>
            <TwoCol>
              <H5 dangerouslySetInnerHTML={{ __html: capitalsphereContent.step4 }} />
              <Img border  src={capitalsphereContent.img4_1} alt={capitalsphereContent.img4_1} theme={{width:"100%"}}/>
            </TwoCol>
            <br/><br/><br/>
            <H2>Results</H2>
            <H5 dangerouslySetInnerHTML={{ __html: capitalsphereContent.results }} />
            <H2>Further Steps</H2>
            <H5 dangerouslySetInnerHTML={{ __html: capitalsphereContent.future }} />
          </InnerContainer>
        </OuterContainer>
        <NextBtn to={capitalsphereContent.link}>
          <div dangerouslySetInnerHTML={{ __html: capitalsphereContent.action }} />
        </NextBtn>
          <br/><br/><br/><br/>
          <Footer/>
      </div>
    );
  }
}

export default Capitalsphere;
