import styled from 'styled-components';
import { device } from './breakpoints.js';
import { Link } from 'react-router-dom';

//For titles on the header
export const H1 = styled.h1 `
    font-family: 'EB Garamond', serif;
    font-weight: 700;
    color: #333333;
    @media ${device.mobileS} {
        font-size: 30px;
    }
    @media ${device.laptopL} {
        font-size: 2.5em!important;
    }
    @media ${device.desktop} {
        font-size: 4.0em!important;
    }
`

//For questions on the homepage and subtitles on about page
export const H2 = styled.h2 `
    font-family: 'EB Garamond', serif;
    font-weight: ${props => props.bold ? "700":""};
    margin: auto;
    padding-bottom: 10px;
    @media ${device.mobileS} {
        font-size: 24px;
    }
    @media ${device.laptopL} {
        font-size: 2em!important;
    }
    @media ${device.desktop} {
        font-size: 3.5em!important;
    }
`

//For all paragraphs
export const H3 = styled.h3 `
    font-family: 'Quicksand', sans-serif;
    color: ${props => props.gray ? "#999999":"#333333"};
    line-height: 1.5em;
    font-weight: 700;
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.laptopL} {
        font-size: 1.75em;!important;
        line-height: 1.5em!important;
    }
    @media ${device.desktop} {
        font-size: 2em!important;
    }
`


//For card title in get involve page
export const H4 = styled.h4 `
    font-family: 'Quicksand', sans-serif;
    color: #333333;
    line-height: 1.45em;
    font-weight: ${props => props.bold ? "700":""};
    @media ${device.mobileS} {
        font-size: 1.25em;
    }
    @media ${device.laptopL} {
        font-size: 1.5em!important;
    }
    @media ${device.desktop} {
        font-size: 2.5em!important;
    }
`

//For all paragraphs
export const H5 = styled.h5 `
    font-family: 'Quicksand', sans-serif;
    color: ${props => props.light ? "#A7A7A7":"#333333"};
    line-height: 1.5em;
    padding-bottom: 10px;
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.laptopL} {
        font-size: 1.20em;!important;
        line-height: 1.5em!important;
    }
    @media ${device.desktop} {
        font-size: 2em!important;
    }
`

//For footer
export const H6 = styled.h6 `
    font-family: 'Quicksand', sans-serif;
    color: #A7A7A7;
    font-weight: ${props => props.light ? "400":"700"};
    text-align: ${props => props.center ? "center":""};
    line-height: 1em;
    @media ${device.mobileS} {
      font-size: 0.8em;
    }
    @media ${device.laptopL} {
      font-size: 1.1em;!important;
      line-height: 1.3em!important;
    }
    @media ${device.desktop} {
      font-size: 1.9em!important;
    }
`

export const Span = styled.span `
    color: ${props => props.subtitle ? "#333":"#C58ADE"};
    font-size: ${props => props.subtitle ? "0.75em":""};
    font-weight: ${props => props.subtitle ? "400":""};
`

export const A = styled.a `
    color: #90C4E9;
`

export const Large = styled.p `
    font-family: 'EB Garamond', serif;
    text-align: center;
    color: rgba(177, 190, 234, 0.4);
    font-size: 108px!important;
    font-weight: 900;
`

export const Btn = styled(Link) `
    cursor: pointer;
    border-radius: 10px;
    border: 2px solid #8095CE;
    text-align: center;
    font-weight: 700;
    padding: 0.5em 1.5em;
    font-family: 'Quicksand', sans-serif;
    letter-spacing: 0.05em;
    transition: 0.2s;
    box-shadow: ${props => props.hover ? "1px 1px 8px 1px #858585":"0px 0px 8px 0px #999999"};
    background-color: ${props => props.hover ? "#8095CE":"white"};
    color: ${props => props.hover ? "white":"#8095CE"};
    &:hover {
        text-decoration: none;
        background-color: #8095CE;
        color: white;
        box-shadow: 1px 1px 8px 1px #858585;
    }
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.tablet} {
        font-size: 1em!important
    }
    @media ${device.laptopL} {
        font-size: 1.5em!important;
    }
    @media ${device.desktop} {
        font-size: 1.75em!important;
    }
`

export const NextBtn = styled(Btn) `
    position: fixed;
    right: calc(50vw - 585px);
    margin: 0;
    bottom: 2em;
    padding: 0.5em 1.2em;
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.tablet} {
        font-size: 1em!important
    }
    @media ${device.laptopL} {
        font-size: 1.2em!important;
    }
    @media ${device.desktop} {
        font-size: 1.55em!important;
    }
`
