import React, { Component } from 'react';
import { H1, H2, H4, H5, Span, NextBtn } from '../css/fonts.js';
import { OuterContainer, InnerContainer, Img, TwoCol } from '../css/container.js';
import { meetingContent } from '../content.js';

import Navbar from '../components/Navbar';
import Footer from '../components/Footer';


class Meeting extends Component {
  render() {
    return (
      <div>
        <Navbar/>
        <OuterContainer>
          <InnerContainer>
            <br/><br/><br/><br/><br/>
            <H1>{meetingContent.title}<Span subtitle>, {meetingContent.subtitle}</Span></H1>
            <H5 light>{meetingContent.team}</H5>
            <H5 dangerouslySetInnerHTML={{ __html: meetingContent.intro }} />
            <Img src={meetingContent.process} alt={meetingContent.process} theme={{width:"100%"}}/>
            <br/><br/><br/>
            <TwoCol>
              <div>
                <H2>My Role</H2>
                <H5 dangerouslySetInnerHTML={{ __html: meetingContent.role }} />
              </div>
              <div>
                <H2>Skills</H2>
                <H5 dangerouslySetInnerHTML={{ __html: meetingContent.skills }} />
              </div>
            </TwoCol>
            <br/>

            <H2>Challenge</H2>
            <H5 dangerouslySetInnerHTML={{ __html: meetingContent.challenge }} />
            <br/>
            <H2>UX Research</H2>
            <H4 bold>Background Research</H4>
            <H5 dangerouslySetInnerHTML={{ __html: meetingContent.step1 }} />
            <TwoCol even>
              <Img border src={meetingContent.img1_1} alt={meetingContent.img1_1} theme={{width:"100%"}}/>
              <Img border src={meetingContent.img1_2} alt={meetingContent.img1_2} theme={{width:"100%"}}/>
            </TwoCol>
            <br/>
            <H5 dangerouslySetInnerHTML={{ __html: meetingContent.step1_1 }} />

            <br/>
            <H4 bold>User Interviews</H4>
            <H5 dangerouslySetInnerHTML={{ __html: meetingContent.step2 }} />
            <TwoCol even>
              <Img border src={meetingContent.img2_1} alt={meetingContent.img2_1} theme={{width:"100%"}}/>
              <Img border src={meetingContent.img2_2} alt={meetingContent.img2_2} theme={{width:"100%"}}/>
            </TwoCol>
            <br/>
            <H5 dangerouslySetInnerHTML={{ __html: meetingContent.step2_1 }} />
            <br/>

            <H4 bold>Aha Moment!</H4>
            <H5 dangerouslySetInnerHTML={{ __html: meetingContent.step3 }} />
            <br/>

            <H4 bold>Probelm Redefinition</H4>
            <H5 dangerouslySetInnerHTML={{ __html: meetingContent.step4 }} />
            <H4 bold>Competitive Analysis</H4>
            <TwoCol>
              <H5 dangerouslySetInnerHTML={{ __html: meetingContent.step5 }} />
              <Img border round src={meetingContent.img5} alt={meetingContent.img5} theme={{width:"100%"}}/>
            </TwoCol>

            <br/><br/><br/>
            <H2>UX Deisgn</H2>
            <H4 bold>Brainstorming</H4>
            <TwoCol>
              <div>
                <H5 dangerouslySetInnerHTML={{ __html: meetingContent.step6 }} />
                <Img border round src={meetingContent.img6_1} alt={meetingContent.img6_1} theme={{width:"85%"}}/>
              </div>
              <Img border round src={meetingContent.img6} alt={meetingContent.img6} theme={{width:"100%"}}/>
            </TwoCol>
            <br/>

            <H4 bold>Paper Prototype</H4>
            <H5 dangerouslySetInnerHTML={{ __html: meetingContent.step7 }} />
            <TwoCol>
              <Img border round src={meetingContent.img7_1} alt={meetingContent.img7_1} theme={{width:"100%"}}/>
              <Img border round src={meetingContent.img7_2} alt={meetingContent.img7_2} theme={{width:"100%"}}/>
            </TwoCol>
            <br/>

            <H4 bold>Low-fi Prototype</H4>
            <TwoCol>
              <H5 dangerouslySetInnerHTML={{ __html: meetingContent.step8 }} />
              <Img border round src={meetingContent.img8} alt={meetingContent.img8} theme={{width:"100%"}}/>
            </TwoCol>
            <br/>

            <H2>Results</H2>
            <H5 dangerouslySetInnerHTML={{ __html: meetingContent.results }} />
            <H2>Further Steps</H2>
            <H5 dangerouslySetInnerHTML={{ __html: meetingContent.future }} />
          </InnerContainer>
        </OuterContainer>
        <NextBtn to={meetingContent.link}>
          <div dangerouslySetInnerHTML={{ __html: meetingContent.action }} />
        </NextBtn>
        <br/><br/><br/><br/>
        <Footer/>
      </div>
    );
  }
}

export default Meeting;
