import React, { Component } from 'react';
import { H1, H5, Span } from '../css/fonts.js';
import { OuterContainer, InnerContainer, Img, TwoCol } from '../css/container.js';
import { meContent } from '../content.js';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';

class AboutMe extends Component {
  handleClick = e => {
    const target = document.getElementById(e.target.title);
    target.scrollIntoView();
  }
  render() {
    return (
      <div>
        <Navbar/>
        <OuterContainer>
          <InnerContainer>
            <br/><br/><br/><br/><br/>
            <H1>{meContent.title}<Span subtitle>, {meContent.subtitle}</Span></H1>
            <br/><br/>
            <TwoCol>
              <H5 dangerouslySetInnerHTML={{ __html: meContent.intro }} />
              <Img border src={meContent.img} alt={meContent.img} theme={{width:"100%"}}/>
            </TwoCol>
            <br/><br/>
            <TwoCol>
              <div>
                <H1>My Passion about UX</H1>
                <H5 dangerouslySetInnerHTML={{ __html: meContent.passion }} />
              </div>
              <div>
                <H1>My Skills</H1>
                <TwoCol even>
                <H5 dangerouslySetInnerHTML={{ __html: meContent.skill1 }} />
                <H5 dangerouslySetInnerHTML={{ __html: meContent.skill2 }} />
                </TwoCol>
              </div>
            </TwoCol>
          </InnerContainer>
        </OuterContainer>
        <br/><br/><br/><br/>
        <Footer/>
      </div>
    );
  }
}

export default AboutMe;
