  duration: "Jan 2019 - Present",
  intro: "Capitalsphere is a Merger&Acquisitions (M&A) website that provides a platform for investors and business owners to match up in order to maximize their profits. The main goal of Capitalsphere is to enable investors and business owners find their best match directly with credible data supported. My goal was to develop a minimal website that appeals users to sign up in order for future's larger website.",
